p{font-family: 'Open Sans', sans-serif;}
.App {
  text-align: center;
}
.header-section{
  padding: 40px 0;
}
.header-logo{
  margin-right: 30px;
}
.header-logo h4.head-icon{

}
.svg-inline--fa {
  font-size: 18px;
}
.header-logo h4{
  font-family: 'Poppins', sans-serif;
}
.header-logo h4 span{
  font-family: 'Rambla', sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  color: gray;
}
.menu{}
.menu ul{
  display: flex;
  justify-content: flex-end;
}
.menu ul li{
  list-style: none;
  margin-right: 22px;
}
.menu ul li:last-child{
  margin-right:0;
}
.menu ul li a{
  font-family: 'Rambla', sans-serif;
  font-size: 18px;
  text-decoration: none;
}



